let html = document.querySelector('#html')
let style = document.querySelector('#style')
let string = `/*戳个太极的哇，不要太急哇


    先整个div
*/
#div1 {
    width: 200px;
    height: 200px;
}
/*


    再盘它
*/
#div1{
    border-radius: 50%;
    box-shadow: 0 0 3px rgba(0,0,0,0.5);
}
/*


    加入阴阳
*/
#div1{
    background: rgb(255,255,255);
    background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 50%, rgba(0,0,0,1) 50%, rgba(0,0,0,1) 100%);
}
/*


    调和一下
*/
#div1::before{
    width: 100px;
    height: 100px;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 50%;
    background: rgb(0,0,0);
    background: radial-gradient(circle, rgba(0,0,0,1) 25%, rgba(255,255,255,1) 25%);
}
#div1::after{
    width: 100px;
    height: 100px;
    bottom: 0;
    right: 50%;
    transform: translateX(50%);
    border-radius: 50%;
    background: rgb(255,255,255);
    background: radial-gradient(circle, rgba(255,255,255,1) 25%, rgba(0,0,0,1) 25%);
}
`
let string2 =''

let n = 0
// console.log(string.length)

let step = () => {
    setTimeout(() => {
        // if(string[n] === '\n'){
        //     string2 += "<br>"
        // }else{
        //     string2 += string[n]
        // }
        string2 +=    string[n] === '\n' ? '<br>' 
                    : string[n] === ' ' ? '&nbsp'
                    : string[n] === '/' ? ''
                    : string[n] === '*' ? ''
                    : string[n]

        html.innerHTML = string2
        style.innerHTML = string.substring(0,n)
        window.scrollTo(0,9999)
        html.scrollTo(0,9999)
        n = n+1
        if(n < string.length){
            step()
        }
    }, 15);
}
//进阶的循环计时器，采用单次计时器的进阶写法完成,便于控制终止条件

step()